	export default {
		data() {
			return {
				email:"",
				password:"",
				disabled:true,
				loading:false
			}
		},
		watch:{
			email(val){
				this.change()
			},
			password(val){
				this.change()
			},
			
		},
		methods: {
			//监听输入框
			change(){
				if(this.email && this.password){
					this.disabled = false
					return;
				}
				this.disabled = true
			},
			//验证层
			check(){
				if(!this.email || this.email==""){
					uni.showToast({
						title:"邮箱不能为空",
						icon:"none"
					})
					return false
				}
				//验证邮箱正则
				var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
                if(!reg.test(this.email)){
					uni.showToast({
						title:"请输入正确的邮箱",
						icon:"none"
					})
					return false
				}
				
				if(!this.password || this.password==""){
					uni.showToast({
						title:"密码不能为空",
						icon:"none"
					})
					return false
				}
				return true	
			},
			//提交
			submit(){
				this.loading = true;this.disabled = true
				if(!this.check()){
					this.loading = false;this.disabled = false;
					return;
					}
				//提交服务器
				uni.showToast({
					title:"提交服务器",
					mask:false,
					duration:1500
				})
				this.loading = false;this.disabled = false
			}
			
		}
	}
